.sendForm {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 43px;
}

.textarea {
    flex: 1;
    min-height: 40px;
    resize: none;
    background-color: #F5F7FB;
    outline: none;
    border: none;
    border-radius: 50px;
    padding-top: 12px;
    padding-bottom: 11px;
    padding-left: 24px;
    overflow: auto;

}
.textarea::placeholder {
    font-size: 14px;
    line-height: 17px;
    color: rgba(0, 0, 0, 0.6);
}

.button {
    padding: 10px 39px 10px 38px;
    margin-left: 44px;
    border: none;
    border-radius: 50px;
    background-color: #0066CC;
    color: #fff;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    cursor: pointer;
}

